"use client";
import Link from "next/link";
import { ShoppingBag, Menu, X } from "lucide-react";
import { useState } from "react";

interface NavbarProps {
  cartCount: number;
  onOpenCart: () => void;
}

export default function Navbar({ cartCount, onOpenCart }: NavbarProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="border-b border-gray-100 sticky top-0 bg-white/90 backdrop-blur-md z-40">
      <div className="max-w-7xl mx-auto px-4 h-16 flex items-center justify-between">
        
        {/* Logo */}
        <Link href="/" className="flex items-center gap-2 font-extrabold text-xl tracking-tighter text-gray-900">
          <div className="bg-gray-900 text-white p-1.5 rounded-lg"><ShoppingBag size={18} /></div>
          StoreLink
        </Link>

        {/* Desktop Links */}
        <div className="hidden md:flex items-center gap-6">
           <Link href="/login" className="text-sm font-bold text-gray-600 hover:text-gray-900">Log In</Link>
           <Link href="/onboarding" className="px-5 py-2 bg-gray-900 text-white rounded-xl text-sm font-bold hover:bg-gray-800 transition">
             Start Selling
           </Link>
           {cartCount > 0 && (
             <button onClick={onOpenCart} className="relative p-2 bg-gray-100 rounded-full hover:bg-gray-200 transition">
               <ShoppingBag size={20} />
               <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full">
                 {cartCount}
               </span>
             </button>
           )}
        </div>

        {/* Mobile Toggle */}
        <div className="flex items-center gap-4 md:hidden">
          {cartCount > 0 && (
             <button onClick={onOpenCart} className="relative p-2 bg-gray-50 rounded-full">
               <ShoppingBag size={20} />
               <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full">
                 {cartCount}
               </span>
             </button>
           )}
          <button onClick={() => setIsMenuOpen(!isMenuOpen)}>
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      {isMenuOpen && (
        <div className="md:hidden border-t border-gray-100 bg-white p-4 flex flex-col gap-4 animate-in slide-in-from-top-2">
           <Link href="/login" className="text-sm font-bold text-gray-600">Log In</Link>
           <Link href="/onboarding" className="px-5 py-3 bg-gray-900 text-white rounded-xl text-sm font-bold text-center">
             Start Selling
           </Link>
        </div>
      )}
    </nav>
  );
}